
































































import Vue from 'vue';
import { viewPost, removePost } from '../../api/posts';
import '@toast-ui/editor/dist/toastui-editor-viewer.css';
import { Viewer } from '@toast-ui/vue-editor';

export default Vue.extend({
	components: {
		Viewer,
	},

	data() {
		return {
			dialog: false,
			title: '',
			username: '',
			content: '',
			created_time: '',
		};
	},

	created() {
		const posts = this.$store.state.posts;
		const post = posts.filter(post => post.id == this.$route.params.id)[0];
		this.title = post.title;
		this.board = post.board;
		this.username = post.username;
		this.created_time = post.created_time.substring(0, 18);
		this.content = post.content;
		this.viewPost();
	},

	methods: {
		async viewPost() {
			try {
				const { data } = await viewPost(this.$route.params.id);
			} catch (error) {
				throw new Error(error);
			}
		},

		async removePost() {
			try {
				this.dialog = false;
				const { data } = await removePost(this.$route.params.id);
				this.$store.commit('openSnackbar', '게시글 삭제가 완료되었습니다!');
				this.$router.replace('/board/list');
			} catch (error) {
				throw new Error(error);
			}
		},
	},
});
