





























































import Vue from 'vue';
import { formatDatetime } from '../../utils/datetime';
import { fetchComments, editComment, removeComment } from '../../api/posts';

export default Vue.extend({
	data() {
		return {
			dialog: false,
			comments: [],
			selectedId: 0,
		};
	},

	filters: {
		formatDatetime,
	},

	created() {
		this.fetchComments();
	},

	methods: {
		async fetchComments() {
			try {
				const { data } = await fetchComments(this.$route.params.id);
				this.comments = data.map(comment => {
					comment.edit = false;
					return comment;
				});
			} catch (error) {
				throw new Error(error);
			}
		},

		async editComment(id, content) {
			try {
				const comment = {
					id,
					content,
				};
				await editComment(comment);
				this.fetchComments();
				this.$store.commit('openSnackbar', '댓글 수정이 완료되었습니다!');
			} catch (error) {
				throw new Error(error);
			}
		},

		async removeComment() {
			try {
				await removeComment(this.selectedId);
				this.dialog = false;
				this.fetchComments();
				this.$store.commit('openSnackbar', '댓글 삭제가 완료되었습니다!');
			} catch (error) {
				throw new Error(error);
			}
		},

		onClickRemove(id) {
			this.selectedId = id;
			this.dialog = true;
		},
	},
});
