















import Vue from 'vue';
import { createComment } from '../../api/posts';

export default Vue.extend({
	data() {
		return {
			content: '',
		};
	},

	methods: {
		async createComment() {
			try {
				const comment = {
					id: this.$route.params.id,
					token: this.$store.state.token,
					content: this.content,
				};
				await createComment(comment);
				this.$router.go();
			} catch (error) {
				throw new Error(error);
			}
		},
	},
});
